.message {
  -webkit-animation: fadein 0.5s;
          animation: fadein 0.5s;
  margin-bottom: 5px;
}
@-webkit-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}
@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

.message-top {
  display: flex;
  flex-direction: row;
}

.client-name {
  font-weight: bold;
  margin-right: 10px;
}

.timestamp {
  color: #AAA;
}
